import { Metadata } from "next";
import SoftwareDetailClient from "./SoftwareDetailClient";
import axiosServer from "@/lib/axios-server";

type Software = {
  id: string;
  title: string;
  version: string;
  file?: string | null;
  external_url?: string | null;
  price?: string;
  license?: string;
  is_free?: boolean;
  one_line_description?: string;
  description?: string;
  developer?: string;
  requirements?: string[];
  platforms?: string[];
  tags?: string[];
  size?: string | null;
  released_at?: string | null;
  updated_at?: string;
  download_count?: number;
  meta_title?: string | null;
  meta_description?: string | null;
  meta_keywords?: string | null;
};

async function getSoftwareData(slug: string): Promise<{ software: Software | null; error: string | null }> {
  try {
    const res = await axiosServer.get(`/content/software/${slug}`);
    
    if (res.data?.success) {
      return { software: res.data.data, error: null };
    } else {
      return { software: null, error: "Failed to load software" };
    }
  } catch (error: any) {
    console.error("Error fetching software:", error);
    
    if (error.response?.status === 404) {
      return { software: null, error: "Software not found" };
    }
    
    return { software: null, error: "Something went wrong while fetching data" };
  }
}

export async function generateMetadata({ params }: { params: { slug: string } }): Promise<Metadata> {
  const { software } = await getSoftwareData(params.slug);

  if (!software) {
    return {
      title: "Software Not Found - Realtime Biometrics",
      description: "The requested software could not be found.",
    };
  }

  const metaTitle = software.meta_title || 
    `${software.title} ${software.version} - Download ${software.is_free ? 'Free' : 'Paid'} | Realtime Biometrics`;
  
  const metaDescription = software.meta_description || 
    software.one_line_description || 
    software.description?.substring(0, 160) ||
    `Download ${software.title} ${software.version} - ${software.is_free ? 'Free' : 'Paid'} software by ${software.developer}`;

  const metaKeywords = software.meta_keywords || 
    software.tags?.join(", ") || 
    `${software.title}, ${software.version}, download, biometrics, attendance software`;

  return {
    title: metaTitle,
    description: metaDescription,
    keywords: metaKeywords,
    openGraph: {
      title: metaTitle,
      description: metaDescription,
      type: "website",
      url: `https://realtimebiometrics.net/software/${params.slug}`,
      siteName: "Realtime Biometrics",
      images: [
        {
          url: "/og-software.jpg",
          width: 1200,
          height: 630,
          alt: software.title,
        },
      ],
    },
    twitter: {
      card: "summary_large_image",
      title: metaTitle,
      description: metaDescription,
      images: ["/og-software.jpg"],
    },
    alternates: {
      canonical: `https://realtimebiometrics.net/software/${params.slug}`,
    },
    robots: {
      index: true,
      follow: true,
      googleBot: {
        index: true,
        follow: true,
        'max-video-preview': -1,
        'max-image-preview': 'large',
        'max-snippet': -1,
      },
    },
  };
}

export async function generateStaticParams() {
  try {
    const res = await axiosServer.get("/content/software");
    const softwares = res.data?.data || [];
    
    return softwares.map((software: Software) => ({
      slug: software.title.toLowerCase().replace(/\s+/g, '-').replace(/[^a-z0-9-]/g, ''),
    }));
  } catch (error) {
    console.error("Error generating static params:", error);
    return [];
  }
}

export default async function SoftwareDetailPage({ params }: { params: { slug: string } }) {
  const { software, error } = await getSoftwareData(params.slug);

  return <SoftwareDetailClient software={software} error={error} />;
}